* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,
li {
  list-style: none;
}
*::before,
*::after {
  box-sizing: border-box;
}
#demo2{
  display: none;
}

#demo{
  position: fixed;
  height: 100vh;
  top:0;
  left:0;
  width:100%;
  overflow-y:scroll;

}
#demo::-webkit-scrollbar {display:none}
#html{
  position: fixed;
  bottom: 0;
  left: 0;
  width:100%;
  height: 50vh;
}
#buttons{
  position: fixed;
  z-index: 10;
  display: flex;
  flex-direction: column;
  right: 0;
  top: 0;
  margin-top: 10px;
  margin-right: 10px;
}
#buttons>button{
  margin-bottom: 10px;
  padding:2px 10px;
  border:1px solid #ffffff;
}